import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faMagnifyingGlass } from "@fortawesome/free-solid-svg-icons";
import Item from "../Item";
import data from "../data";

const Search = () => {
  const navigate = useNavigate(); // useHistory  
  const [query, setQuery] = useState(""); //    
  const [searchInitiated, setSearchInitiated] = useState(false);
  const [searchResults, setSearchResults] = useState([]); //   

  const searchChange = (e) => {
    if (searchInitiated) {
      setSearchInitiated(false); //        
    }
    setQuery(e.target.value);
  };

  const performSearch = () => {
    setSearchInitiated(true); //   
    if (query.trim()) {
      const allItems = Object.values(data).flat();
      const normalizedQuery = query.replace(/\s+/g, "").toLowerCase(); //     
      const filteredData = allItems.filter((item) =>
        item.title.replace(/\s+/g, "").toLowerCase().includes(normalizedQuery)
      );
      setSearchResults(filteredData); //   
    }
    setQuery(""); //     
  };

  const searchKey = (e) => {
    if (e.key === "Enter") {
      performSearch();
    }
  };

  const searchButton = (e) => {
    e.preventDefault();
    performSearch();
  };
  const closeSearch = () => {
    navigate(-1); // ''     
  };
  return (
    <div className="searchWrap">
      <div className="searchInput">
        <FontAwesomeIcon
          icon={faMagnifyingGlass}
          className="searchIcon"
          onClick={searchButton}
        />
        <form onSubmit={searchButton}>
          <input
            type="text"
            value={query}
            placeholder=" "
            onChange={searchChange}
            onKeyDown={searchKey}
          />
        </form>
        <span onClick={closeSearch}></span>
      </div>
      <div className="searchResult">
        {searchInitiated && searchResults.length === 0 && (
          <p>  .</p>
        )}
        {searchResults.length > 0 &&
          searchResults.map((item) => <Item key={item.id} item={item} />)}
      </div>
    </div>
  );
};

export default Search;
